<template>
	<section class="home">
		<Workout :workout="currWorkout" />
		<button @click="moveToNextWorkout">
			<p>&rarr;</p>
		</button>
	</section>
</template>

<script>
import Workout from '@/cmps/Workout'
export default {
	components: { Workout },
	computed: {
		currWorkout() {
			return this.$store.getters.currWorkout
		},
	},
	methods: {
		moveToNextWorkout() {
			this.$store.dispatch('moveToNextWorkout')
			this.$store.dispatch('loadCurrWorkout')
		},
	},
	created() {
		this.$store.dispatch('loadCurrWorkout')
	},
}
</script>
