import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
	{
		path: '/edit/:id',
		component: () => import('../views/EditWorkout.vue'),
	},
	{
		path: '/edit',
		component: () => import('../views/Edit.vue'),
	},
	{
		path: '/',
		component: Home,
	},
]

const router = new VueRouter({
	routes,
})

export default router
